import React from "react"
import Typewriter from "typewriter-effect"
import "../styles/contact.scss"

export default function Contact({ close }) {

  return (
    <div className="contactContainer">
      <div style={{ margin: "auto", minWidth: "100%" }}>
        <h1 className="contactH1">
          <Typewriter
            onInit={typewriter => {
              typewriter
                .typeString("Contact me")
                .start()
                .callFunction(function (state) {
                  state.elements.cursor.style.display = "none"
                })
            }}
          />
        </h1>
        <ul className="contact-list">
        <li>
          <img
            className="svgIcon"
            src={`https://cdn.jsdelivr.net/npm/simple-icons/icons/linkedin.svg`}
            alt="Tech icon"
          />
          <a href="https://www.linkedin.com/in/ningliu4096">linkedin.com/in/ningliu4096</a>
        </li>
        <li>
          <img
            className="svgIcon"
            src={`https://cdn.jsdelivr.net/npm/simple-icons/icons/gmail.svg`}
            alt="Tech icon"
          />
          <a href="mailto:liuning4096@gmail.com">liuning4096@gmail.com</a>
        </li>
      </ul>
      </div>
    </div>
  )
}
